import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import Swal from 'sweetalert2'; //  SweetAlert2
import { addtocart, removeFromCart } from './cartslice'; //       cartslice

function Carttollkit() {
    const dispatch = useDispatch();
    const cart = useSelector((state) => state.cartslice);

    //   
    const totalAmount = cart.reduce((total, item) => total + item.price, 0);

    const handleDelete = (id) => {
        Swal.fire({
            title: 'Are you sure?',
            text: "You won't be able to revert this!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes, delete it!'
        }).then((result) => {
            if (result.isConfirmed) {
                dispatch(removeFromCart(id));
                Swal.fire(
                    'Deleted!',
                    'Your item has been deleted.',
                    'success'
                );
            }
        });
    };

    return (
        <>
        <div className='container mt-2 ' style={{padding:"60px 20px "}}>
            <h1>Shopping Cart</h1>

        </div>
            {cart.length === 0 ? (
                <p>No items in the cart</p>
            ) : (
                <>
               
               <div className='container mt-2'>
               <h3>
               Total: <span style={{ color: "green", fontSize: "19px" }}>{totalAmount.toFixed(2)}$</span>
</h3>


               </div>


                   
                    <div className='container mt-3'>
                    <table className="table">
                        <thead>
                            <tr>
                                <th scope="col">#</th>
                                <th scope="col">Image</th>
                                <th scope="col">Title</th>
                                <th scope="col">Price</th>
                                <th scope="col">Actions</th>
                            </tr>
                        </thead>
                        <tbody>
                            {cart.map((item, index) => (
                                <tr key={item.id}>
                                    <th scope="row">{index + 1}</th>
                                    <td>
                                        <img src={item.image} alt={item.title} style={{ width: '50px', height: 'auto' }} />
                                    </td>
                                    <td>{item.title}</td>
                                    <td>{item.price}$</td>
                                    <td>
                                        <button 
                                            className="btn btn-danger" 
                                            onClick={() => handleDelete(item.id)}
                                        >
                                            Delete
                                        </button>
                                         
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                    </div>
                     
                </>
            )}
        </>
    );
}

export default Carttollkit;
