import { useEffect, useState } from 'react';
import './Productlist.css';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';

function Productlist() {
  const api_url = "https://fakestoreapi.com/products";
  const [products, setProducts] = useState([]);

  useEffect(() => {
    fetch(api_url)
      .then((res) => res.json())
      .then((data) => setProducts(data))
      .catch((err) => console.error('Error fetching products:', err));
  }, []);

  const deleteProduct = (productId) => {
    // Confirmation dialog using SweetAlert2
    Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.isCofnirmed) {
        fetch(`${api_url}/${productId}`, {
          method: 'DELETE',
        })
          .then((res) => {
            if (!res.ok) {
              return res.text().then(text => {
                console.error('Failed to delete product. Response:', text);
                throw new Error('Failed to delete product');
              });
            }

            setProducts(products.filter((product) => product.id !== productId));
            // Success alert using SweetAlert2
            Swal.fire(
              'Deleted!',
              'Your product has been deleted.',
              'success'
            );
          })
          .catch((err) => console.error('Error deleting product:', err));
      }
    });
  };


  return (
    <div className="container-fluid  CRUDS">
      <h1 className='container mt-2'>CRUDS</h1>
        <div className="row justify-content-center">
            <div className="col-md-9 col-lg-10">
                <div className="table-wrapper">
                    <table className="table table-responsive">
                        <thead>
                            <tr>
                                <th scope="col">ID</th>
                                <th scope="col">Title</th>
                                <th scope="col">Description</th>
                                <th scope="col">Price</th>
                                <th scope="col">Operations</th>
                            </tr>
                        </thead>
                        <tbody>
                            {products.map((product) => (
                                <tr key={product.id}>
                                    <th scope="row">{product.id}</th>
                                    <td>{product.title.length > 15 ? `${product.title.slice(0, 15)}...` : product.title}</td>
                                    <td className="description">{product.description.length > 50 ? `${product.description.slice(0, 50)}...` : product.description}</td>
                                    <td className='price'>{product.price}$</td>
                                    <td>
                                        <button className="btn btn-danger btn-sm me-2 mb-1" onClick={() => deleteProduct(product.id)}>Delete</button>
                                        <Link to={`/products/${product.id}`} className="btn btn-primary btn-sm me-2  mb-1">View</Link>
                                        <Link to={`/edit-product/${product.id}`} className="btn btn-warning btn-sm  mb-1">Edit</Link>
                                    </td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>
            </div>
        </div>
    </div>
);

}

export default Productlist;
