// Card.js
import { useDispatch, useSelector } from 'react-redux';
import { useEffect } from 'react';
import { fetchproduct } from './sliceproduct';
import { Link } from 'react-router-dom';
import { addtocart } from './cartslice';
function Card() {
  const dispatch = useDispatch();
  const products = useSelector((state) => state.products);

  useEffect(() => {
    dispatch(fetchproduct());
  }, [dispatch]);

  if (!Array.isArray(products)) {
    return <p>Loading...</p>;
  }

 
  return (

    <div className="container  mt-4">
      <h2> Products Cart</h2>
      <div className="row">
        {products.length > 0 ? (
          products.map((pro) => (
            <div className="col-lg-3 col-md-4 col-sm-6 col-12 mb-4  " key={pro.id}>
              <div className="card " style={{ width: '100%' ,height:"420px",overflow:"hidden" }}>
                <img className="card-img-top" src={pro.image} alt="Card image cap" style={{height:"230px "}}/>
                <div className="card-body">
                  <h5 className="card-title">{pro.title.slice(0,15)}..</h5>
                  <p className="card-text mb-1">{pro.description.slice(0,23)}...<Link style={{fontWeight:"600"}} className='link' to={`/card/${pro.id}`}><br/>Read more </Link></p>
                  <p className="card-text" style={{ fontSize:"17px",fontWeight:"600",padding:"0",margin:"0"}}>Price: <span style={{color:"green" ,fontSize:"19px",fontWeight:"700"}}> {pro.price}$</span></p>
                  <button  href="#" className="btn btn-primary mt-2" onClick={()=> dispatch(addtocart(pro))}>Add To Card   </button >
                </div>
              </div>
            </div>
          ))
        ) : (
          <p>No products available</p>
        )}
      </div>
    </div>
  );
}

export default Card;
