import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";
import Swal from 'sweetalert2'; 


function Producttestdetails() {
  const [details, setDetails] = useState([]);  
  const { productsId } = useParams();

  useEffect(() => {
    fetch(`https://fakestoreapi.com/products/${productsId}`)  
      .then((res) => res.json())
      .then((data) => setDetails([data]));  
  }, [productsId]);  


 
const navigate=  useNavigate()
  return (
    <>
<div className="container mb-4" style={{ marginTop: "100px" }}>
  <div className="row justify-content-center">
    {details.map((el) => (
      <div className="cards mb-3" key={el.id}  >
        <div className="row g-0">
          <div className="col-md-6 text-center">
            <img src={el.image} alt={el.title} className="img-fluid rounded-start" style={{maxHeight:"500px"}} />
          </div>
          <div className="col-md-6 p-3">
            <div className="card-body">
              <h5 className="card-title">{el.title}</h5>
              <p className="card-text">{el.description}</p>
              <p className="card-text fs-5 fw-700"><small className="text-muted">Price:  <span style={{fontSize:'19px',fontWeight:"700",color:"green"}}>{el.price}$</span></small></p>
              <button className="btn btn-info" onClick={() => navigate(-1)}>Back</button>
            </div>
          </div>
        </div>
      </div>
    ))}
  </div>
</div>



 

    </>
  );
}

export default Producttestdetails;
