import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

function CardDetails() {
  const { cartId } = useParams();
  const api_url = "https://fakestoreapi.com/products";
  const [cardDetails, setCardDetails] = useState(null);
const navigate=useNavigate()


  useEffect(() => {
    fetch(`${api_url}/${cartId}`)
      .then((res) => res.json())
      .then((data) => setCardDetails(data))
      .catch((error) => console.error("Error fetching data:", error));
  }, [cartId]);

  if (!cardDetails) return <p>Loading...</p>;

  return (
    <div className="container my-4 " style={{paddingTop:"100px "}}>
      <div className="row">
        <div className="col-md-6 text-center">
          <img style={{maxHeight:"500px"}}
            src={cardDetails.image}
            alt={cardDetails.title}
            className="img-fluid rounded"
          />
        </div>
        <div className="col-md-6 p-4 mt-4">
          <h2>{cardDetails.title}</h2>
          <p><strong>Price:</strong>   <span style={{color:"green",fontSize:"19px",fontWeight:"700"}}>{cardDetails.price}$</span></p>
          <p>{cardDetails.description}</p>
          <p><strong>Category:</strong> {cardDetails.category}</p>
          <button className="btn btn-info text-light" onClick={()=>navigate(-1)}>Back</button>
        </div>
      </div>
    </div>
  );
}

export default CardDetails;
