import { useEffect, useState } from "react";
import { useNavigate, useParams } from "react-router-dom";

function Productdetails() {
  const api_url = "https://fakestoreapi.com/products";
  const { productId } = useParams();   
  const [productDetails, setProductDetails] = useState(null);


  

const navigate = useNavigate();


  useEffect(() => {
    fetch(`${api_url}/${productId}`)
      .then((res) => res.json())
      .then((data) => setProductDetails(data))
      .catch((err) => console.error('Error fetching product details:', err));
  }, [productId]);

  if (!productDetails) {
    return <div className="container text-center mt-5"><h3>Loading...</h3></div>;
  }


  return (
    <div className="container mt-5 " style={{padding:"20px "}}>
      <div className="row">
        <div className="col-md-6 d-flex justify-content-center">
          <img style={{maxHeight:"500px ", marginBottom:"20px "}} 
            src={productDetails.image} 
            alt={productDetails.title} 
            className="img-fluid rounded shadow-sm" 
          />
        </div>

        <div className="col-md-6">
          <h2 className="mb-3">{productDetails.title}</h2>
          <h4 className="text-secondary mb-3">{productDetails.category}</h4>
          <p className="mb-3">{productDetails.description}</p>
          <h3 className="text-primary mb-4">${productDetails.price}</h3>
          
          <button style={{margin:"10px ",padding:"10px 20px "}}
            onClick={() => navigate(-1)} 
            className="btn btn-secondary "
          >
            Back
          </button>
        </div>
      </div>
    </div>
  );
}

export default Productdetails;
