import { useEffect, useState } from "react";
import { Link } from "react-router-dom";

function Producttest() {
  const api_url = "https://fakestoreapi.com/products";
  const [products, setProducts] = useState([]);
  const [categories, setCategories] = useState([]);
  useEffect(() => {
    fetch(api_url)
      .then((res) => res.json())
      .then((data) => setProducts(data));
  }, []);

  useEffect(() => {
    fetch("https://fakestoreapi.com/products/categories")
      .then((res) => res.json())
      .then((data) => setCategories(data));
  }, []);
  

  
  const filtercard = (catname) => {
    fetch(`https://fakestoreapi.com/products/category/${catname}`)
      .then((res) => res.json())
      .then((data) => setProducts(data)); 
  };

  return (
    <>
      <div className="container" style={{ marginTop: "40px" }}>
       
        <div className="row">
          {/* Render category buttons */}
          <div className="col-12 mb-3 text-center">
            {categories.map((cat, index) => (
              <button key={index} style={{textTransform:"capitalize",fontWeight:"600"}} className="btn btn-primary text-light m-1" onClick={()=>filtercard(cat)}>
                {cat}
              </button>
            ))}
          </div>

          {/* Render products */}
          {products.map((pro) => (
            <div className="col-12 col-md-4 col-lg-3 col-sm-6" key={pro.id}>
              <div className="card m-2" style={{ height: "400px",overflow:"hidden" }}>
                <img
                  className="card-img-top"
                  src={pro.image}
                  alt="Card image cap"
                  style={{ height: "250px" }}
                />
                <div className="card-body">
                  <h5 className="card-title">{pro.title.slice(0, 16)}...</h5>
                  <p className="card-text" style={{whiteSpace:"nowrap",overflow:"hidden",padding:"0 4px 0 0  "}}>{pro.description}...</p>
                  <Link to={`./products/${pro.id}`} className="btn btn-primary">
                    Details
                  </Link>
                </div>
              </div>
            </div>
          ))}
        </div>
      </div>
    </>
  );
}

export default Producttest;
